function getDate(type) {
    const date = new Date();

    let year = date.getFullYear();
    let month = date.getMonth() + 1;
    let day = date.getDate();

    if (type === 'start') {
        year = year - 60;
    } else if (type === 'end') {
        year = year + 2;
    }
    month = month > 9 ? month : '0' + month;
    day = day > 9 ? day : '0' + day;

    return `${year}-${month}-${day}`;
}

var util = require('../../common/util.js');
var getCookie = util.getCookie;
var setCookie = util.setCookie;
var changeWarn = util.changeWarn;
var getWarnCookie = util.getWarnCookie;
var myAjax2 = util.myAjax2;
var backgroundAudioManager = wx.getBackgroundAudioManager();
export default {
    data() {
        return {
            title: '系统设置',
            toast: 0,
            toastTxt: '',
            loading: 0,

            userInfo: null,
            deviceNos: '', // 设备号
            accessToken: null,

            breathNum: '--', // 呼吸值 -100_无效值，其他为正常值
            deviceStatus: null, // 设备状态 3_离床，4_在床，5_光纤故障，6_离线，9_传感器负荷，10_信号弱
            heartNum: '--', // 心率值 65436_无效值，其他为正常值
            markTime: null, // 发生的时间戳
            motionNum: null, // 体动值 0_正常，3_轻微体动，4_中度体动，5_大幅体动，-100_无效值
            timer: null,

            device: '', // 是否监控离床
            deviceTimes: '', // 离床持续时间
            deviceStart: '', // 监控时段开始
            deviceEnd: '', // 监控时段结束
            heart: '', // 是否监控心率
            heartUp: '', // 心率过快峰值
            heartDown: '', // 心率过慢峰值
            breath: '', // 是否监控呼吸率
            breathUp: '', // 呼吸过快峰值
            breathDown: '', // 呼吸过慢峰值
            motion: '', // 是否监控体动值
            motionTimes: '', // 大幅体动持续时间
            motionStart: '', // 体动监控时段开始
            motionEnd: '', // 体动监控时段结束

            warnNing: 0,
            warningText: '',
            warnNo: '',
            warnDeviceTime: '',
            warnHeartTime: '',
            warnBreathTime: '',
            warnMotionTime: ''
        };
    },
    onLoad() {
        let _this = this
        _this.timer = setInterval(function() {
            util.changeWarn(_this);
            // console.log('setting页面同步一次报警数据')
        }, 1000);
    },
    onLaunch() {},
    onShow() {
        let _this = this;
        let accessToken = util.getCookie('accessToken');
        let deviceNos = getCookie('deviceNos');
        if (!accessToken) {
            wx.redirectTo({
                url: '../login/index'
            });
        } else if (!deviceNos) {
            uni.redirectTo({
                url: '../code/index'
            });
        } else {
            _this.accessToken = accessToken;
            _this.deviceNos = deviceNos;
            changeWarn(_this, backgroundAudioManager);
            getWarnCookie(_this);
        }
    },
    onHide() {},
    onUnload() {
        clearInterval(this.timer);
    },
    methods: {
        bindPickerChange(e) {
            // console.log('picker发送选择改变，携带值为：' + e.target.value);
            this.index = e.target.value;
        },
        bindMultiPickerColumnChange(e) {
            // console.log('修改的列为：' + e.detail.column + '，值为：' + e.detail.value);
            this.multiIndex[e.detail.column] = e.detail.value;
            this.$forceUpdate();
        },
        bindTime01Change(e) {
            this.deviceStart = e.target.value;
        },
        bindTime02Change(e) {
            this.deviceEnd = e.target.value;
        },
        switch1Change(e) {
            this.device = e.target.value;
            util.setWarnCookie(this);
        },
        switch2Change(e) {
            this.heart = e.target.value;
            util.setWarnCookie(this);
        },
        switch3Change(e) {
            this.breath = e.target.value;
            util.setWarnCookie(this);
        },
        switch4Change(e) {
            this.motion = e.target.value;
            util.setWarnCookie(this);
        },
        deviceTimesChange(e) {
            let value = e.target.value;
            if (value < 1 || value > 300) {
                value = 1;
                util.showToastBox(this, '请输入1-300的数字');
            }
            this.deviceTimes = value;
            util.setWarnCookie(this);
        },
        deviceStartChange(e) {
            this.deviceStart = e.target.value;
            util.setWarnCookie(this);
            if (this.deviceStart > this.deviceEnd) {
                util.showToastBox(this, '开始时间不可晚于结束时间, 错误时间段将导致无法做出提醒!');
            }
        },
        deviceEndChange(e) {
            this.deviceEnd = e.target.value;
            util.setWarnCookie(this);
            if (this.deviceStart > this.deviceEnd) {
                util.showToastBox(this, '开始时间不可晚于结束时间, 错误时间段将导致无法做出提醒!');
            }
        },
        heartDownChange(e) {
            let value = e.target.value;
            if (value < 1 || value > 180) {
                value = 1;
                util.showToastBox(this, '请输入1-180的数字');
            }
            this.heartDown = value;
            util.setWarnCookie(this);
        },
        heartUpChange(e) {
            let value = e.target.value;
            if (value < 1 || value > 180) {
                value = 1;
                util.showToastBox(this, '请输入1-180的数字');
            }
            this.heartUp = value;
            util.setWarnCookie(this);
        },
        breathDownChange(e) {
            let value = e.target.value;
            if (value < 1 || value > 180) {
                value = 1;
                util.showToastBox(this, '请输入1-180的数字');
            }
            this.breathDown = value;
            util.setWarnCookie(this);
        },
        breathUpChange(e) {
            let value = e.target.value;
            if (value < 1 || value > 180) {
                value = 1;
                util.showToastBox(this, '请输入1-180的数字');
            }
            this.breathUp = value;
            util.setWarnCookie(this);
        },
        motionTimesChange(e) {
            let value = e.target.value;
            if (value < 1 || value > 300) {
                value = 1;
                util.showToastBox(this, '请输入1-300的数字');
            }
            this.motionTimes = value;
            // util.warnRule.motionTimes = value;
            util.setWarnCookie(this);
        },
        motionEndChange(e) {
            this.motionEnd = e.target.value;
            util.setWarnCookie(this);
            if (this.deviceStart > this.deviceEnd) {
                util.showToastBox(this, '开始时间不可晚于结束时间, 错误时间段将导致无法做出提醒!');
            }
        },
        motionStartChange(e) {
            this.motionStart = e.target.value;
            util.setWarnCookie(this);
            if (this.deviceStart > this.deviceEnd) {
                util.showToastBox(this, '开始时间不可晚于结束时间, 错误时间段将导致无法做出提醒!');
            }
        },
        /**
         * 关闭报警
         */
        audioPause() {
            util.audioPause(this, backgroundAudioManager);
        }
    }
};
